@import url('https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap');

.setRoot {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80%;
  font-family: 'Black Han Sans', sans-serif;
}

.setMain {
  display: flex;
  justify-content: center;

  width: 90%;
  height: 80%;
}

.setLeftPanel {
  width: 50%;
  height: 100%;
}

.setRightPanel {
  background-color: #E3F2FD;
  border-radius: 20px;
  width: 50%;
  height: 100%;
  padding: 1%;
  overflow-y: scroll;
  overflow-x: hidden;
  margin: 0 0 0 5px;
}

.itemMain {
  height: 100px;
  width: 100%;
  display: flex;
  padding: 5px;
  border-bottom: 1px solid #6B6B6B;
  cursor: pointer;
}
.itemImgMain {

  width: 35%;
  height: 100%;
  padding: 0 2% 0 0;
}

.itemImg {
  width: 100%;
  height: 95%;
  border-radius: 20px;
}

.itemExpMain {
  width: 65%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.itemTag {
  font-size: 12px;
  color: #666666;
}

.itemRow {
  display: flex;
  justify-content: space-between;
}

.itemMenuBtn {
  font-size: 14px;
  border-radius: 10px;
  padding: 2% 5% 2% 5%;
  background-color: #1976D2; 
  color: white;
}
.cursorMap {
  padding: 1%;
  
}
.modalMain {
  height: 500px;
}
.menuSelect {
  height: 10%;
}
.setModal {
  background-color: #1976D2;
  padding: 2%;
  width: 5%;
  border-radius: 10px;
  color: white;
  cursor:pointer;
}


.showList  {
  padding: 10% 0 0;
}
.footer {
  display: flex;
  justify-content: flex-end;
}
.nextBtn {
  cursor: pointer;
  margin: 0 5% 0 0;
  background-color: #1976D2;
  color: white;
  padding: 1%;
  border-radius: 12px;
}
